.wrap {
  max-width: 1170px;
  margin: auto;
  padding: 0 15px;
}

.header .slider .item {
  position: relative;
}

.header .slider .item .wrap {
  position: absolute;
  display: flex;
  align-items: center;
  text-align: center;
  top: 110px;
  left: 0;
  right: 0;
}

.header .slider .item .wrap .logo {
