  font-family: 'OpenSans-Regular';
  font-size: 12px;
}

.header .info .wrap .languages a {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: #8546a7;
  color: #fff;
  margin: 5px;
  padding-top: 5px;
  text-decoration: none;
}

.header .info .wrap .languages a:hover {
  background: #fff;
  color: #8546a7;
  transition: .5s;
}

.header .info .wrap .languages a:nth-child(2) {
  background: #fff;
  color: #8546a7;
}

.header .info .wrap .languages a:nth-child(2):hover {
